import * as React from 'react'
import TopBar from '../Topbar'
import { Box, Button, Alert } from '@mui/material';
import "../App.css"
import { red, yellow, green } from '@mui/material/colors';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Unstable_Grid2';
import { styled } from '@mui/material/styles';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import { Link } from 'react-router-dom';
import { InputForm } from '../inputBoxs';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import SelectSubject from '../selectSubject';
import { BrowserView, MobileView, isBrowser, isMobile } from 'react-device-detect';


export function PushNewScore({ data, user }) {
  const [students, setStudents] = React.useState([])
  const [open, setOpen] = React.useState(false);

  function handleClose() {
    setOpen(false)
  }

  const [inputValues, setInputValues] = React.useState(Array(46));
  const [summeryValue, setSummeryValue] = React.useState(Array(46));

  const [gradeTitle, setGradeTitle] = React.useState()
  const [gradeSubject, setGradeSubject] = React.useState()
  const [annousment, setAnnousment] = React.useState()

  function handleChange(input, value) {
    if (input === "gradeTitle") {
      setGradeTitle(value)
    }
    else if (input === "gradeSubject") {
      setGradeSubject(value)
    }
    else if (input === "annousment") {
      setAnnousment(value)
    }
  }
  /*const handleInputChange = (index, value) => {
    console.log(index, value, "000151656464")
    var updatedValues = [...];
    updatedValues[index] = value;
    console.log(updatedValues[index])
    console.log(inputValues, updatedValues)
    (updatedValues);
  };*/
  const handleGradeChange = (index, newValue) => {
    const newGrades = inputValues;
    newGrades[index] = newValue;
    setInputValues(newGrades);
  };
  const handleSummeryChange = (index, newValue) => {
    const newSummery = summeryValue;
    newSummery[index] = newValue;
    setSummeryValue(newSummery);
  };
  const handleSubmit = (m) => {
    setOpen(true)
    console.log({
      method: m,
      score: {
        title: gradeTitle,
        subject: gradeSubject,
        annousment: annousment,
        scoreData: inputValues,
        summeryData: summeryValue,
      }

    })

    fetch("/api/uploadnewscore", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        method: m,
        score: {
          title: gradeTitle,
          subject: gradeSubject,
          annousment: annousment,
          scoreData: inputValues,
          summeryData: summeryValue,
        }

      }),
    })
      .then(res => res.json())
      .then(res => {
        if (res.ok) {
          setOpen(false)
          window.location.href = "/"
        }
        else {
          alert("!!")
        }

      })
      .catch(() => {
        alert("")
      })
  };



  React.useEffect(() => {
    fetch("/api/getallstudents", {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({}),
    })
      .then(res => res.json())
      .then(res => {
        if (res.ok) {
          console.log(".......0", res)
          var list = []
          for (let i = 0; i < res.data.result.length; i++) {
            if (res.data.result[i].userid.includes("s")) {


              var object = res.data.result[i]
              object.scoreInput = <TextField type='number' min="0" max="100" value={inputValues[i]} onChange={(e) => handleGradeChange(i, e.target.value)} label="" variant="standard" />
              object.summeryInput = <TextField value={summeryValue[i]} onChange={(e) => handleSummeryChange(i, e.target.value)} label="" variant="standard" />

              console.log(object, i, "nioh", inputValues[i])
              list.push(object)
            }
          }
          setStudents(list)

        } else {
          alert("!!")
        }

      })
  }, [])


  return (
    <>
      <TopBar logined={true} data={data.data} user={user} title={""} />

      <Box sx={{ p: 3 }}>
        <h1></h1>
        <p>
          <br />
          
        </p>
        <TextField label="" variant="standard"
          value={gradeTitle}
          sx={{ width: "100%" }}
          onInput={(e) => handleChange("gradeTitle", e.target.value)}

        />
        <p></p>

        <SelectSubject onChangeFunc={handleChange} params={"gradeSubject"}
        />
        <MobileView></MobileView>

        <p></p>
        <TextField
          label=""
          multiline
          sx={{ width: "100%" }}
          rows={2}
          variant="standard"
          value={annousment}
          onInput={(e) => setAnnousment(e.target.value)}

        />
        <p></p>
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 650 }}>
            <TableHead>
              <TableRow>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell></TableCell>
                <TableCell>()</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {students.map((row, i) => (
                <TableRow
                  key={row.id}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    {row.id}
                  </TableCell>
                  <TableCell>{row.username}</TableCell>
                  <TableCell>{row.scoreInput}</TableCell>
                  <TableCell>{row.summeryInput}</TableCell>

                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <p></p>

        <Button variant='contained' onClick={() => handleSubmit("save")}></Button>
      </Box>

      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {""}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
             {gradeTitle} ...<br />
            
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}></Button>
        </DialogActions>
      </Dialog>
    </>
  )
}